<template>
		<div>
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
					<a >首页</a>
				</router-link>
			</div>
			<div class="appstoreHeight">
				<div id="container"></div>
				<div id="panel"></div>
			</div>
		</div>
</template>
<script>
	import Lib from '@/assets/js/Lib';
	import ClubFoot from "../../components/ClubFoot";
	import { Toast } from 'mint-ui';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	showTitle:"点击收起",
		    	place:'',
			    isShow: false,
			    shopData:[],
			    latitude:'',//纬度
			    longitude:''//经度
			}
		},
		components:{
		    ClubFoot
		},
		methods:{
			loadData:function(){
				var _this=this,json={};
				var mapObj,geolocation;
				_this.place= _this.$route.params.place;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;

				 var map = new AMap.Map("container", {
			        resizeEnable: true,
			        center: [_this.place.myLng, _this.place.myLat],//地图中心点
			        zoom: 10 //地图显示的缩放级别
			    });
				new AMap.Marker({
				    position:[_this.place.myLng, _this.place.myLat],//marker所在的位置
				    map:map//创建时直接赋予map属性
				});
				new AMap.Marker({
			        map: map,
			        position: [_this.place.shopLng, _this.place.shopLat]
			    });
				//也可以在创建完成后通过setMap方法执行地图对象
				
			    //步行导航
			    var walking = new AMap.Walking({
			        map: map,
			        panel: "panel"
			    }); 
			    //根据起终点坐标规划步行路线
			    walking.search([_this.place.myLng, _this.place.myLat], [_this.place.shopLng, _this.place.shopLat]);
			},
			Spanclick(cur){
				if(this.isShow==cur){
					this.isShow=false
					// this.title="点击展开"
				}else{
					this.isShow=cur;
					// this.title="点击收起"
				}
			},
			viewMap(pos){
				var _this=this;
			}
		},
		mounted:function(){
             this.$nextTick(function(){
             	this.loadData();
             });
        }
	}
</script>
<style scope>
	 .topuphead a{
		color: #333;
	}
	#container{
		top:0.85rem;
		height: 80%;
	}
</style>


