<template>
		<div class="Topup Integral">
			<div class="Topuptitle">
				<div class="clearfix topuphead">
					<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
					<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
						<a >首页</a>
					</router-link>
				</div>
				<p>当前粉币</p>
				<p class="residue">{{fenbi}}<span>粉币</span></p>
			</div>
			<div class="Topupoptions">
				<div class="options clearfix">
					<!-- 有数据 -->
					<div class="IntegralData" v-if="hasData">
						<ul
						  v-infinite-scroll="loadMore"
						  infinite-scroll-disabled="loading"
						  infinite-scroll-distance="10">
						  <li class="IntegralDataCom clearfix"  v-for="(item,index) in fbData">
						  		<div class="IntegralDataComLe w60 fl">
									<p >{{item.itemName}}</p>
									<p><a href="">{{item.createDate}}</a></p>
								</div>
								<div class=" IntegralDataComRi fr w30 ">
									<span>{{item.number}}粉币</span>
								</div>
						  </li>
						</ul>
						<p :class="{msgType:loading}">{{msg}}</p>
					</div>
					<!-- 没有数据 -->
					<NoMessage v-else messageCom="暂无粉币记录" :class="{integralNoB:!isHied}"></NoMessage>
				</div>
				<ClubFoot></ClubFoot>
				<div class="buttonHeight" v-if="hasData"></div>
				<div class="integralButton" v-if="isHied">
					<button class="active" type="button" @click='fbExchange' name="" value="" >粉币兑换</button>
				</div>
			</div>
		</div>
</template>
<script >
	import ClubFoot from "@/components/ClubFoot";
	import NoMessage from "@/components/NoMessage";
	import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	hasData:false,
		    	loading:false,
		    	isHied:false,
		    	fenbi:0,
		    	busId:0,
		    	url:'',
		    	page:1,
		    	msg:'加载更多数据',
		    	fbData:[],
		    	pageData:[]
			}
		},
		components:{
		    ClubFoot,
		    NoMessage
		},
		methods:{
			loadData:function(){
				var _this=this;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json['page']=_this.page;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findCardrecordNewFenbi_GET,
	                'type': 'GET',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	console.log('fenbi',data);
	                	if(data.code==0&&data.data.page.length>0){
	                		_this.fbData=data.data.page;
	                		_this.hasData=true;
	                		_this.pageData=_this.fbData;
	                	}
	                	_this.fenbi=data.data.fenbi;
	                }
				})
			},
			loadMore() {
				var _this=this;
				_this.loading = true;
				if(_this.pageData!=0){
			  		_this.page+=1;
			  	}else{
			  		return false;
			  	}
			  	var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json['page']=_this.page;
				setTimeout(() => {
			  		Lib.M.ajax({
						'url':memberAPI.activeAPI.findCardrecordNewFenbi_GET,
		                'type': 'GET',
		                'data':{
		                   "json":json
		                },
		                'success':function (data){
		                	console.log(data.data.page.length);
		                	_this.pageData=[];
		                	if(data.data.page.length!=0){
		                		if(data.code==0&&data.data.page.length>0){
			                		_this.pageData=data.data.page;
			                		for(let i=0;i<_this.pageData.length;i++){
			                			_this.fbData.push(_this.pageData[i]);
			                		}
			                	}
		                	}else{
		                		_this.msg='到底了';
		                	}
		                	_this.loading = false;
		                }
					})
				}, 1000);
			},
			fbExchange(){
				var _this=this;
				_this.$router.push({ name: 'powderShop', params: { busId: _this.busId }});
			}
		},
		created:function(){
            this.loadData();

        }

	}
</script>
<style scope>
	
</style>


