<template>
	<!-- 资料修改详细页面 -->
		<div class="completeinfor">
			<div class="bghead">
				<div class="clearfix topuphead">
					<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
					<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
						<a >首页</a>
					</router-link>
				</div>
			</div>
			<div class="editprofile " >
				<div class="completeinforCom">
					<div class=" completeinforTit msgPosition">
						<label for="username">姓名</label>
						<input type="text"  name="" value="" placeholder="必填"   id="username" v-model="name">
						<i class=" tip carAni" v-if="isCheck.isnameTip">{{tipMsg.isnameMsg}}</i>
					</div>
					<div class="sex">
						<label for="sex" class="fl">性别</label>
						<input type="button" name="" v-model='sex'   :class="{sexColor:isColor}" placeholder="请选择" id="sex"  @click="sexChoose">
						<i class="icon iconfont icon-you"  @click="sexChoose"></i>
					</div>
					<div class="phone msgPosition">
						<label for="phone">手机号</label>
						<input type="number" name="" disabled="!changePhone" value="" placeholder="必填" id="phone" pattern="\d" v-model.number="phone" @focus="clearPhone">
						<i class=" tip carAni" v-if="isCheck.phoneTip">{{tipMsg.phoneMsg}}</i>
						<a href="javascript:;" v-if="isGetCode" class="phonemessage">{{timeleft}}秒后可重发</a>
						<a href="javascript:;" v-if="isCode" @click="getCode"><span>获取验证码</span></a>
						<a href="javascript:;" v-if="!changePhone" @click="changePhones"><span >更换手机</span></a>
					</div>
					<div v-if="changePhone" class="msgPosition">
						<label for="authCode">验证码</label>
						<input type="text" name="" value="" placeholder="必填" v-model="vcode"  id="authCode">
					</div>
				</div>
				<div class="completeinforCom">
					<div class=" completeinforTit msgPosition" >
						<label for="password">登陆密码</label>
						<input id="password" v-model="password" placeholder="选填" :onkeyup="changPassword(password)" />
						<i class=" tip carAni" v-if="isCheck.passwordTip">{{tipMsg.passwordMsg}}</i>
					</div>
					<div v-if='memberOption.birthShow==1' class="msgPosition">
						<label for="dateBirth">出生日期</label>
						<input type="button" style="width:4.5rem" name="" v-model="birth" value="" readonly="readonly" @click="clickBirth" :placeholder='memberOption.birthOption==1?"必填":"选填"' id="dateBirth">
						<i class=" tip" v-if="isCheck.birthTip">{{tipMsg.birthMsg}}</i>
					</div>
					<div v-if='memberOption.addrShow==1' class="msgPosition">
						<label for="address">地址</label>
						<input type="button" style="width:4.5rem"  v-model="addr" @click="addProvince" readonly="readonly" name="" value="" :placeholder='memberOption.addrOption==1?"必填":"选填"' id="address"  >
						<i class=" tip carAni" v-if="isCheck.addrTip">{{tipMsg.addrMsg}}</i>
					</div>
					<div v-if='memberOption.addrDetailShow==1' class="msgPosition">
						<label for="addressdetails">地址详情</label>
						<input type="text" style="width:4.5rem" name="" v-model="addressdetail"  value="" :placeholder='memberOption.addrDetailOption==1?"必填":"选填"' id="addressdetails">
						<i class=" tip carAni" v-if="isCheck.addressdetailTip">{{tipMsg.addressdetailMsg}}</i>
					</div>
					<div v-if='memberOption.getMoneyShow==1' class="msgPosition">
						<label for="annualIncome" >年收入</label>
						<input type="text" name="" v-model.number="getMoney"  value="" :placeholder='memberOption.getMoneyOption==1?"必填":"选填"' id="annualIncome"><i>元</i>
						<i class=" tip carAni" v-if="isCheck.getMoneyTip">{{tipMsg.getMoneyMsg}}</i>
					</div>
					<div v-if='memberOption.mailShow==1' class="msgPosition">
						<label for="email">邮箱</label>
						<input type="text" name="" v-model="email" value="" :placeholder='memberOption.mailOption==1?"必填":"选填"' id="email">
						<i class=" tip carAni" v-if="isCheck.emaliTip">{{tipMsg.emailMsg}}</i>
					</div>
					<div v-if='memberOption.cardShow==1' class="msgPosition">
						<label for=" identitycard">身份证</label>
						<input type="text" name="" v-model="sfCard" value="" :placeholder='memberOption.cardOption==1?"必填":"选填"' id="identitycard">
						<i class=" tip carAni" v-if="isCheck.sfCardTip">{{tipMsg.sfCardMsg}}</i>
					</div>
					<div class="fileImage clearfix" v-if='memberOption.uploadCard==1'>
						<label for="" class="fl">身份证</label>
						<div class="MainImage w30 fl">
							<div>
								<img :src="img.imgTop" v-if="isImgTop">
								<i class="icon iconfont icon-icon20" v-else></i>
								<span>主页</span>
								<!-- android -->
								<input type="file" @change="upload('top')" capture="camera" :class="{isOnLoad:isImgTop}"  accept="image/*"  name="" id="shooting" v-if="!isIos">
								<!-- ios -->
								<input type="file" @change="upload('top')" :class="{isOnLoad:isImgTop}"  accept="image/*"  name="" id="shooting" v-else>
								<!-- capture="camera" -->
							</div>
							
						</div>
						<div class="VicePage w30 fl">
							<div>
								<img :src="img.imgBottom"  v-if="isImgBottom">
								<i class="icon iconfont icon-icon20" v-else></i>
								<span>副页</span>
								<!-- android -->
								<input type="file" :class="{isOnLoad:isImgBottom}" capture="camera" accept="image/*" name="" id="albumChoose" @change="upload('bot')"  v-if="!isIos">
								<!-- ios -->
								<input type="file" :class="{isOnLoad:isImgBottom}" accept="image/*" name="" id="albumChoose" @change="upload('bot')" v-else>
							</div>
						</div>					
					</div>
				</div>
				<div class="completeinforSubit">
					<input class="active" type="button" name="" value="保存" @click="getAnswer()">
				</div>

			</div>
			<!-- 拍摄弹出窗 -->
			<!-- <transition enter-active-class="animated fadeInUp" leave-active-class="animated fadeOutDown">
				<div class="fileUpload" v-if="isfileUpload">
					<div class="fileUploadBg">
						<p><input type="file"  accept="image/gif, image/jpeg,image/png,image/jpg" capture="camera" name="" id="shooting"> 拍摄</p>
						<p><input type="file" accept="image/gif, image/jpeg,image/png,image/jpg" name="" id="albumChoose" @change="upload">从相册选择</p>
						<p class="fileUpHeight"></p>
						<p @click="fileCancelUpload">取消</p>
					</div>
				</div>
			</transition> -->
			<!-- 性别选择弹出框 -->
			<div class="overlayer" @touchmove.prevent v-show="isCheckPopUp">
			<transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
				<div class="fileUpload" v-if="isSexSelect">
					<div class="fileUploadBg">
						<p><input type="radio" v-model='man' @click="sureCancelman" value="男" id="Boy"> 男</p>
						<p><input type="radio"  v-model='gril' @click="sureCancelgril" name="" value="女" id="Girl">女</p>
						<p><input type="radio"  v-model='privary' @click="keepSecret" name="" value="保密" id="Girl">保密</p>
						<p class="fileUpHeight"></p>
						<p><input type="radio"   @click="fileCancel" name="" value="取消" id="secret">取消</p>
					</div>
				</div>
			</transition>
			<mt-datetime-picker
			    v-model="pickerVisible"
				type="date"
				ref="picker"
				:endDate="endDate"
				:startDate="startDate"
				year-format="{value} 年"
				month-format="{value} 月"
				date-format="{value} 日"
				:visible-item-count="5"
				@confirm="handleConfirm"
				@cancel="checkinCancel" >
			</mt-datetime-picker>
			<!-- 地址联动 -->
			<div class="address" v-show="addressShow">  
	            <div class="picker-toolbar">  
	                <span class="mint-datetime-action mint-datetime-cancel" @click="cancleaddress">取消</span>  
	                <span class="mint-datetime-action mint-datetime-confirm" @click="selectaddress">确定</span>  
	            </div>  
	            <mt-picker :slots="slots" :itemHeight="40" value-key="city_name"  @change="onValuesChange" :visibleItemCount="5"></mt-picker>  
	        </div> 
	        <div v-show="isSignPop">
				<SignExchange :titleCom='titleCom' :icon="hasIcon"></SignExchange>
			</div>
		</div>
			<ClubFoot></ClubFoot>
		</div>
</template>
<script >
	import ClubFoot from "@/components/ClubFoot";
	import SignExchange from "@/components/SignExchange"; 
	import Lib from '@/assets/js/Lib';
	import axios from 'axios';
	import { Toast } from 'mint-ui';
	export default{
		data() {
		    return {
		    	isEditNow:true,
		    	isEdit:false,
		    	isfileUpload:false,
		    	isSexSelect:false,
		    	isColor:false,
		    	sex:'请选择',
		    	isImgTop:false,
		    	isImgBottom:false,
		    	addressShow:false,
		    	isTrue:false,
		    	isCode:false,
		    	isGetCode:false,
		    	timeleft:120,
		    	oldPWlength:0,
		    	newPWlength:0,
		    	isCheckPopUp:false,

		    	hasIcon:false,
		    	isSignPop: false,
		    	titleCom:'',

		    	password:'',
		    	editPassword:[],
		    	value1:'',
		    	value2:'',
		    	value3:'',
		    	value1Id:'',
		    	value2Id:'',
		    	value3Id:'',
		    	cursurPosition:0,
		    	isGetCode:false,
		    	startDate:new Date(new Date().getFullYear() - 50, 0, 1),
		    	endDate:new Date(),
		    	imgCur:1,
		    	busId:0,
		    	isIos:false,
		    	url:'',
		    	img:{
		    		imgTop:'',
		    		imgBottom:''
		    	},
		    	changePhone:false,
		    	memberData:[],
		    	memberOption:[],

		    	memberParamter:[],
		    	cityList:[],
		    	countyList:[],
		    	provinceList:[],

		    	man:'男',
		    	gril:'女',
		    	privary:'保密',

		    	name:'',
		    	pwd:'',
		    	phone:'',
		    	addr:'',
		    	birth:'',
		    	addressdetail:'',
		    	getMoney:'',
		    	email:'',
		    	sfCard:'',
		    	vcode:'',

		    	pickerVisible:new Date(new Date().getFullYear() - 3, 5, 21),
		    	isCheck:{
		    		isnameTip:false,
		    		passwordTip:false,
		    		addrTip:false,
		    		birthTip:false,
		    		phoneTip:false,
		    		addressdetailTip:false,
		    		getMoneyTip:false,
		    		emaliTip:false,
		    		sfCardTip:false
		    	},
		    	tipMsg:{
		    		passwordMsg:'6-21字母和数字组成的密码',
		    		isnameMsg:'姓名不能为空',
		    		addrMsg:'地址不能为空',
		    		phoneMsg:'手机号码格式错误',
		    		birthMsg:'出生日期不能为空',
		    		addressdetailMsg:'地址详情不能为空',
		    		getMoneyMsg:'年收入不能为空',
		    		emailMsg:'邮箱不能为空',
		    		sfCardMsg:'身份证号码不能为空',
		    		authCodeMsg:'验证码不能为空'
		    	},
		    	slots: [
			        {
			          flex: 1,
			          values: [],
			          className: 'slot1',
			          textAlign: 'left'
			        },
			        {
			          flex: 1,
			          values: [],
			          className: 'slot2',
			          textAlign: 'center'
			        },
			        {
			          flex: 1,
			          values: [],
			          className: 'slot3',
			          textAlign: 'right'
			        }
			    ]
			}
		},
		components:{
		    ClubFoot,
		    SignExchange
		},
		methods:{
			loadData:function(){
				var _this=this;
				 //获取浏览器的userAgent,并转化为小写
			    var ua = navigator.userAgent.toLowerCase();
			    //判断是否是苹果手机，是则是true
			    _this.isIos = (ua.indexOf('iphone') != -1) || (ua.indexOf('ipad') != -1);
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findMemberUser_POST,
                    'type': 'POST',
                    'data':{
                       "json":json
                    },
                    'success':function (data){
                    	console.log('completeinfor',data);
                    	if(data.code==0){
                    		_this.memberData=data.data.member;
                    		_this.memberParamter=data.data.memberParamter;
                    		_this.cityList=data.data.cityList;
                    		_this.countyList=data.data.countyList;
                    		_this.provinceList=data.data.provinceList;

                    		_this.name=_this.memberData.name;
                    		_this.phone=_this.memberData.phone;
                    		if(_this.memberData.sex!=0){
                    			_this.isColor=true;
                    		}
                    		_this.sex=(_this.memberData.sex==1?'男':(_this.memberData.sex==2?'女':'保密'));
                    		_this.memberOption=data.data.memberOption;
                    		_this.sfCard=_this.memberData.cardId;
                    		_this.email=_this.memberData.email;
                    		_this.addressdetail=_this.memberParamter.address;
                    		_this.getMoney=_this.memberParamter.getMoney;
                    		console.log(_this.memberData.birth)
                    		if(_this.memberData.birth!=null){
                    			_this.birth=Lib.M.formatTime(_this.memberData.birth,true);
                    		}
                    		
                    		// 获取地址
                    		_this.value1Id=_this.memberParamter.provinceCode;
                    		_this.value2Id=_this.memberParamter.cityCode;
                    		_this.value3Id=_this.memberParamter.countyCode;
                  			for(let i=0;i<_this.provinceList.length;i++){
                  				if(_this.provinceList[i].city_code==_this.memberParamter.provinceCode){
                  					_this.value1=_this.provinceList[i].city_name;
                  					break;
                  				}
                  			}
                  			for(let i=0;i<_this.cityList.length;i++){
                  				if(_this.cityList[i].city_code==_this.memberParamter.cityCode){
                  					_this.value2=_this.cityList[i].city_name;
                  					break;
                  				}
                  			}
                  			for(let i=0;i<_this.countyList.length;i++){
                  				if(_this.countyList[i].city_code==_this.memberParamter.countyCode){
                  					_this.value3=_this.countyList[i].city_name;
                  					break;
                  				}
                  			}
                  			_this.addr=_this.value1+_this.value2+_this.value3;
                    	}
                    }
				})
			},
			EditProfile(){
				this.isEditNow=false;
		    	this.isEdit=true;
			},
			// 获取省
			addProvince(){
				var _this=this;
				_this.addressShow=true;
				_this.isCheckPopUp=true;
				_this.slots[0].values=_this.provinceList;
			},
			//获取市
			getCityData(data){
				var _this=this;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findCityByCityCode_POST,
                    'type': 'POST',
                    'data':{
                       "json":data
                    },
                    'success':function (data){
                    	_this.city=data.data;
                    	console.log("city",_this.city);
                    	if(_this.city!=undefined||_this.city.length>0){
	                		var json={};
	                		json['cityCode']=_this.value2Id;
							json['requestUrl']=_this.url;
							json['busId']=_this.busId;
							_this.getDistrictData(json);
	                	}
                    }

				})
				
			},
			//获取区
			getDistrictData(data){
				var _this=this;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findCityByCityCode_POST,
                    'type': 'POST',
                    'data':{
                       "json":data
                    },
                    'success':function (data){
                    	_this.district=data.data;
                    	console.log("district",_this.district)
                    	if(_this.district!=undefined||_this.district.length>0){
	                		_this.slots[1].values=_this.city;
							_this.slots[2].values=_this.district;
	                	}
                    }

				})
			},
			onValuesChange(picker, values) {
				var _this=this,json={};
				console.log(values);
				if(values[0]!=undefined){
					var provinceVal=values;
					_this.value1=provinceVal[0].city_name;
					_this.value1Id=provinceVal[0].city_code;
					if(values[1]!=undefined){
						_this.value2=provinceVal[1].city_name;
						_this.value2Id=provinceVal[1].city_code;
					}else{
						_this.value2Id=_this.memberParamter.city_code;
					}
					if(values[2]!=undefined){	
						console.log(values)
						_this.value3=provinceVal[2].city_name;
						_this.value3Id=provinceVal[2].city_code;
					}else{
						_this.value3Id=_this.memberParamter.countyCode;
					}
					json['cityCode']=provinceVal[0].city_code;
					json['requestUrl']=_this.url;
					json['busId']=_this.busId;
					_this.getCityData(json)
				}
				
		    },
		     cancleaddress(){
		    	var _this=this;
		    	_this.isCheckPopUp=false;
		    	_this.addressShow=false;
		    },
			selectaddress(){
		    	var _this=this;
		    	_this.addressShow=false;
		    	_this.isCheckPopUp=false;
		    	_this.addr=_this.value1+_this.value2+_this.value3;
		    },
			//密码星号显示
			// getTxt1CursorPosition(){
			//  	var _this=this;
	  //           var $input = document.getElementById("password");
	  //           if($input.selectionStart){//非IE
	  //               _this.cursurPosition= $input.selectionStart;
	  //           }else{//IE
	  //               try{
	  //               var range = document.selection.createRange();
	  //               range.moveStart("character",-$input.value.length);
	  //               _this.cursurPosition=range.text.length;
	  //               }catch(e){
	  //                _this.cursurPosition = 0;
	  //               }
	  //           }
	
	  //       },
			changPassword(password){
				var _this=this;
				var nowWord=password.slice(password.length-1,password.length);
				if(password==''){
					_this.isCheck.passwordTip=false;
				}
				if(_this.editPassword.length!=0){
					if(_this.cursurPosition != 0){
						if(password.length<_this.oldPWlength){
							_this.editPassword.splice(_this.oldPWlength-1,1)
						}
					}else{
						if(password.length<_this.oldPWlength){
							_this.editPassword.splice(password.length,1)
						}
					}
				}
				
				if(nowWord==''){
					return false
				}
				if(nowWord!='*'){
					_this.editPassword.push(nowWord)
				}
				_this.password=password.replace(/./g,'*')
				_this.oldPWlength=password.length;
				
			},
			// 更换手机
			changePhones(){
				var _this=this;
				document.getElementById("phone").disabled=false;
				_this.changePhone=true;
				_this.isCode=true;
			},
			clearPhone(){
				var _this=this;
				_this.phone='';
			},
			//获取验证码
			getCode(){
				var _this=this;
				var json={};
				_this.isTrue=Mobilephone(_this.phone);
				if(!_this.isTrue){
					_this.isCheck.phoneTip=true;
					return false;
				}
				_this.isCheck.phoneTip=false;
				_this.isGetCode=true;
				_this.isCode=false;
				json["busId"]=_this.busId;
				json["phone"]=_this.phone;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.sendMsg_POST,
                    'type': 'POST',
                    'data':{
                       "json":json
                    },
                    'success':function (data){
                    	console.log("phonedata",data);
                    }

				})
				var time=setInterval(function(){
					_this.timeleft--;
					if(_this.timeleft==0){
						_this.isGetCode=false;
						_this.isCode=true;
						_this.timeleft=120;
						clearTimeout(time);
					}
				},1000);
			},
			//生日选择
			clickBirth(){
				var _this=this;
				_this.isCheckPopUp=true;
				_this.$refs.picker.open();
			},
			checkinCancel(){
				var _this=this;
				_this.isCheckPopUp=false;
				_this.birth='';
				_this.$refs.picker.close();
			},
			handleConfirm(value){
				var _this=this;
				_this.isCheckPopUp=false;

				_this.pickerVisible=Lib.M.formatTime(value,true)
				_this.birth=_this.pickerVisible
			},
			fileCancelUpload(){
				var _this=this;
				_this.isfileUpload=false;
			},
			//性别选择
			sexChoose(){
				var _this=this;
				_this.isCheckPopUp=true;
				_this.isSexSelect=true;
			},
			sureCancelman(){
				var _this=this;
				_this.isSexSelect=false;
				_this.isCheckPopUp=false;
				_this.isColor=true;
				console.log(_this.man)
				_this.sex=_this.man;
				_this.memberData.sex=1;
				// _this.isCheck.issexTip=false;
			},
			sureCancelgril(){
				var _this=this;
				_this.isSexSelect=false;
				_this.isCheckPopUp=false;
				_this.sex=_this.gril;
				console.log(_this.gril)
				_this.memberData.sex=2;
				_this.isColor=true;
				// _this.isCheck.issexTip=false;
			},
			fileCancel(){
				var _this=this;
				_this.isSexSelect=false;
				_this.sex='请选择';
				_this.isCheckPopUp=false;
				_this.memberData.sex=0;
				_this.isColor=false;
				// _this.isCheck.issexTip=false;
			},
			keepSecret(){
				var _this=this;
				_this.isSexSelect=false;
				_this.sex='保密';
				_this.isCheckPopUp=false;
				_this.memberData.sex=0;
				_this.isColor=true;
			},
			//上传图片
			upload(pos){
				var _this=this,json={};
				var e = event || window.event; //获取事件对象
				var files = e.target.files || e.dataTransfer.files;
				let formData = new FormData();
				formData.append("file", files[0]); 
				formData.append("busId", _this.busId); 
				formData.append("requestUrl", _this.url); 
			 	if (!files.length)
			  	 return;
					  	// // 判断图片格式  
		      //   if(!(files[0].type.indexOf('image')==0 && files[0].type && /\.(?:jpg|png|gif)$/.test(files[0].name)) ){  
		      //       Toast({
		      //       	message:'请输入格式为jpg|png|gif的图片',
		      //       	duration:1000
		      //       });
		      //       return ;  
		      //   };
		        let config = {
                    headers: {
                      "Content-Type": "multipart/form-data"
                    }
                };
		        var url=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.updateImage_POST;
		        console.log(url)
		        axios.post(url, formData, config).then(response => {
		        	console.log("imgdata",response);
		        	console.log(pos)
		        	if(response.data.code==0){
		        		if(pos=='top'){
		        			var urlImg=response.data.data[0].url.substring(1,response.data.data[0].url.length);
		        			_this.img.imgTop=response.data.data[0].pathHead+urlImg;
		        			_this.isImgTop=true;
		        			console.log(_this.img.imgTop)
		        		}
		        		if(pos=='bot'){
		        			var urlImg=response.data.data[0].url.substring(1,response.data.data[0].url.length);
		        			_this.img.imgBottom=response.data.data[0].pathHead+urlImg;
		        			_this.isImgBottom=true;
		        		}
		        		Toast({
			            	message:'上传成功',
			            	duration:1000
			            });
		        	}
		        	
		        }).catch(function(err){
		        	Toast({
		            	message:'上传失败',
		            	duration:1000
		            });
		        })
			},
			// createImage(file) {
			// 	var _this=this;
			//   	var image = new Image();
			//   	var reader = new FileReader();
			//   	reader.readAsDataURL(file);
			//   	reader.onload = (e) => {
			// 	  	if(_this.imgCur==1){
			// 	  		_this.img.imgTop = e.target.result;
			// 	  		_this.isImgTop=true;
			// 	  	}else if(_this.imgCur==2){
			// 	  		_this.img.imgBottom = e.target.result;
			// 	  		_this.isImgBottom=true;
			// 	  	}
				    
			// 	};
			// },
			tipHide(pos){
				var _this=this;
				_this.isSignPop = pos;
				if (pos) {
		        	setTimeout(() => {
		          		_this.isSignPop = !pos;
		        	}, 2000);
		        }
			},
			getAnswer(){
				var _this=this,result,json={};
				_this.pwd=_this.editPassword.join('');
				if(_this.name==''){
					_this.isCheck.isnameTip=true;
					return false;
				}else{
					result=Lib.M.checkName(_this.name);
					if(result){
						_this.isCheck.isnameTip=true;
						_this.tipMsg.isnameMsg='请输入正确的姓名';
						return false;
					}
					_this.isCheck.isnameTip=false;
				}
				if(_this.phone==''){
					return false;
				}
				if(_this.changePhone){
					if(_this.vcode==''){
						Toast({
			            	message:'验证码有误',
			            	duration:1000
			            });
						return
					}
				}
				if(_this.authCode==''&&_this.changePhone==true){
					_this.titleCom=_this.tipMsg.authCodeMsg;
					_this.tipHide(true);
					return false;
				}

				if(_this.pwd!=''){
					result=Lib.M.checkPassWord(_this.pwd);
					if(result){
						_this.isCheck.passwordTip=true;
						return false;
					}
					_this.isCheck.passwordTip=false;
				}else{
					_this.isCheck.passwordTip=false;
				}
				
				if(_this.memberOption.addrShow==1){
					if(_this.addr==''||_this.addr==null){
						if(_this.memberOption.addrDetailOption==1){
							_this.isCheck.addrTip=true;
							return false;
						}
					}else{
						_this.isCheck.addrTip=false;
					}
				}
				if(_this.memberOption.birthShow==1){
					if(_this.birth==''||_this.birth==null){
						if(_this.memberOption.birthOption==1){
							_this.isCheck.birthTip=true;
							return false;
						}
					}else{
						_this.isCheck.birthTip=false;
					}
				}
				if(_this.memberOption.addrDetailShow==1){
					if(_this.addressdetail==''||_this.addressdetail==null){
						if(_this.memberOption.addrDetailOption==1){
							_this.isCheck.addressdetailTip=true;
							return false;
						}
					}else{
						_this.isCheck.addressdetailTip=false;
					}
				}
				if(_this.memberOption.getMoneyShow==1){
					if(_this.getMoney==''||_this.getMoney==null){
						if(_this.memberOption.getMoneyOption==1){
							_this.isCheck.getMoneyTip=true;
							return false;
						}
					}else{
						result=Lib.M.checkNumber(_this.getMoney);
						if(result){
							_this.isCheck.getMoneyTip=true;
							_this.tipMsg.getMoneyMsg='请输入大于零的整数';
							return false;
						}
						_this.isCheck.getMoneyTip=false;
					}
				}
				if(_this.memberOption.mailShow==1){
					if(_this.email==''||_this.email==null){
						if(_this.memberOption.mailOption==1){
							_this.isCheck.emaliTip=true;
							return false;
						}
					}else{
						result=Lib.M.checkEmail(_this.email);
						if(result){
							_this.isCheck.emaliTip=true;
							_this.tipMsg.emailMsg='邮箱格式错误';
							return false;
						}
						
						_this.isCheck.emaliTip=false;
					}
				}
				if(_this.memberOption.cardShow==1){
					if(_this.sfCard==''||_this.sfCard==null){
						if(_this.memberOption.cardOption==1){
							_this.isCheck.sfCardTip=true;
							return false;
						}
					}else{
						result=Lib.M.checkidCard(_this.sfCard);
						if(!result){
							_this.isCheck.sfCardTip=true;
							_this.tipMsg.sfCardMsg='身份证号码错误';
							return false;
						}
						_this.isCheck.sfCardTip=false;
					}
				}
				json['busId']=parseInt(_this.busId);
				json["requestUrl"]=_this.url;
				json['phone']=_this.phone;
				json['pwd']=_this.pwd;
				json['vcode']=_this.vcode;
				json['name']=_this.name;
				json['gender']=_this.memberData.sex;
				json['birth']=_this.birth;
				json['email']=_this.email;
				json['cardId']=_this.sfCard;
				json['provincecode']=_this.value1Id;
				json['city']=_this.value2Id;
				json['countyCode']=_this.value3Id;
				json['address']=_this.addressdetail;
				json['getMoney']=_this.getMoney;
				json['imageurls']=_this.img.imgTop+','+_this.img.imgBottom;
				console.log(json);
				Lib.M.ajax({
					'url':memberAPI.activeAPI.updateMemberUser_POST,
                    'type': 'POST',
                    'data':{
                       "json":json
                    },
                    'success':function (data){
                    	if(data.code==0){
                    		Toast({
				            	message:'保存成功',
				            	duration:1000
				            });
                    	}
                    },
                    'error':function(err){
                    	Toast({
			            	message:'保存失败',
			            	duration:1000
			            });
                    }

				})
			}
		},
		created:function(){
            this.loadData();

        }
	}
</script>
<style scope>
	/*资料编辑*/
	input{
		background: #fff;
	}
	
</style>


